export function loginValidation(login: string) {
    if (login.length === 0)
        return { error: true, errorText: 'Pole login nie moe by puste' }
    if (login.length < 4)
        return {
            error: true,
            errorText: 'Login musi skada si z co najmniej 4 znakw'
        }
    if (login.length > 15)
        return {
            error: true,
            errorText: 'Login nie moe by duszy ni 15 znakw'
        }
    return { error: false, errorText: '' }
}

export function passwordValidation(password: string) {
    if (password.length < 6)
        return {
            error: true,
            errorText: 'Haso musi skada si z co najmniej 6 znakw'
        }
    return { error: false, errorText: '' }
}
